.catalog-container {
    width: 70%;
    @media screen and (max-width: 750px) {
       width: 100% 
    }
    padding: 5px 15px 30px;
    margin: 0 auto;
    background: rgba(255, 255, 255, .7);
    border-radius: 5px;

    ul {
        list-style-type: disc;
    }

    a {
        text-decoration: underline;
    }
}