.item-container {
    position: relative;
    width: 40%;
    margin: 20px auto;
    padding: 10px 10px 30px 10px;
    @media screen and (max-width: 750px) {
        width: 100%;
        margin: 10px 0 0 0;
        padding-bottom: 60px;
    }
    background: rgba(255, 255, 255, .8);
    border-radius: 10px;
    box-sizing: border-box;

    .extra, .time {
        position: absolute;
        bottom: 5px;
        font-size: 14px;
        color: #808a87;
        @media screen and (max-width: 750px) {
            left: 30px;
        }
    }

    .extra {
        left: 30px;
        @media screen and (max-width: 750px) {
            bottom: 10px;
        }
    }

    .time {
        right: 30px;
        @media screen and (max-width: 750px) {
            bottom: 35px;
        }
    }
}