.search-container {
    z-index: 100;
    position: fixed;
    top: 80px;
    left: 30px;
    width: 500px;
    @media screen and (max-width: 750px) {
        top: 5px;
        left: 5px;
        width: 350px;
    }
    box-sizing: border-box;

    .search-input {
        position: absolute;
        top: 0;
        width: 0px;
        height: 30px;
        transition: all .3s ease-in-out;
        line-height: 30px;
        padding: 0 14px;
        border-radius: 15px;
        border: 1px solid #BEE7E9;
        box-sizing: border-box;

        &:focus {
            box-shadow:#BEEDC7  0 5px 10px 0px;
        }
    }

    .input-show {
        width: 100%;
    }

    .search-icon-container {
        transition: all .3s ease-in-out;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0px;
        width: 30px;
        height: 30px;
        box-sizing: border-box;
        border-radius: 50%;
        background: #BEE7E9;
        cursor: pointer;
    }

    .icon-show {
        left: 470px;
        @media screen and (max-width: 750px) {
            left: 320px;
        }
    }

    @keyframes show {
        from {
            width: 0;
            opacity: 0;
        }
        to {
            width: 100%;
            opacity: 1;
        }
    }
    .show-container {
        animation: show .5s ease-in-out forwards;
        position: absolute;
        top: 35px;
        left: 0;
        width: 100%;
        padding: 5px 0;
        background: #fff;
        border-radius: 10px;

        .show-item {
            box-sizing: border-box;
            width: 100%;
            padding: 3px 10px;
            border-bottom: 1px solid #836F64; 
            font-size: 12px;
            cursor: pointer;

            &:nth-last-of-type(1) {
                margin-bottom: 0;
                border-bottom: 0;
            }

            .title {
                float: left;
                font-size: 14px;
                color: #131B1B;
            }

            .extra {
                float: right;
                margin-right: 10px;
                color: #836F64;
            }

            .time {
                float: right;
                color: #836F64;
            }
        }
    }
}