ul {
    margin-top: 0 !important;
    list-style-type: none;
}
.tab-container {
    z-index: 100;
    position: sticky;
    top: 0;
    width: 100%;
    height: 60px;
    padding: 0 60px;
    background: rgba(76, 76, 76, .8);
    box-sizing: border-box;

    .tab-item {
        position: relative;
        float: left;
        padding: 0 5px;
        margin-left: 20px;
        line-height: 60px;
        color: #e4e4e4;
        transition: all .3s ease-in-out;

        &:hover {
            cursor: pointer;
            color: #0baaa3;
        }

        &:not(.active):hover:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 5px;
            background: #0baaa3;
        }
    }
    .active {
        color: #0baaa3;

        &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 5px;
            background: #0baaa3;
        }
    }
}

.tab-container-phone {
    z-index: 100;
    position: sticky;
    top: 0;
    width: 100%;
    height: 40px;
    padding: 0;
    margin: 0;
    background: rgba(76, 76, 76, .8);

    .icon {
        position: absolute;
        right: 5px;
        top: 5px;
        transition: all .2s ease-in-out;
    }

    .icon.list-active {
        box-sizing: content-box;
        top: 0;
        right: 0;
        padding: 5px;
        background: rgb(255, 255, 255);
    }

    @keyframes listShow {
        from {
            opacity: 0;
            transform: translateX(-100%);
        }
        to {
            opacity: 1;
            transform: translateX(0);
        }
    }
    
    .list-container-phone {
        animation: listShow .5s ease-in-out forwards;
        position: absolute;
        top: 40px;
        right: 0;
        width: 50%;
        padding: 0;
        margin-bottom: .8rem;
        box-sizing: border-box;
        background: rgb(255, 255, 255);
        border-radius: 5px;
        border-top-right-radius: 0;

        .tab-item {
            position: relative;
            width: 100%;
            padding: 5px;
            text-align: center; 
            box-sizing: border-box;
            border-bottom: 1px dashed #ccc;

            &:nth-last-of-type(1) {
                border-bottom: 0;
            }
        }

        .tab-item.active {
            color: #0baaa3;

            &:after {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                width: 10px;
                height: 100%;
                background: #0baaa3;
            }
        }
    }
}